export const types = [
    '',
    '  ',
    '',
    '',
    '',
    '',
    '',
    '',
    ''];

export class FilterOptions {
    constructor() {
        this.types = [
            '',
            '  ',
            '',
            '',
            '',
            '',
            '',
            '',
            '']
    }

    get colors() {
        return [
            {class: 'beige', name: ''},
            {class: 'whitesnake', name: ''},
            {class: 'shocking-blue', name: ''},
            {class: 'yellow', name: ''},
            {class: 'king-crimson', name: ''},
            {class: 'deep-purple', name: ''},
            {class: 'black-sabbath', name: ''}]
    }

    get sizes() {
        return {
            odd: [...Array(10).keys()].filter(n => n%2 > 0).map(n => 10 + n),
            even: [...Array(10).keys()].filter(n => n%2 === 0).map(n => 12 + n)
        }
    }

    get heelSizes() {
        return {
            odd: [1, 3, 5, 7, 9],
            even: [2, 4, 6, 8, 10]
        }
    }

    get reasons() {
        return [
            '',
            '',
            '',
            '',
            '',
            '',
            ' ',
        ]        
    }

    get seasons() {
        return [
            '',
            '',
            '',
            ''
        ]                
    }
}


